(function() {
    'use strict';

    angular
        .module('oncoscape', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'ui-rangeSlider', 'toastr']);

})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osTools', tools);

    /** @ngInject */
    function tools() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/tools/tools.html',
            controller: ToolsController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function ToolsController(osApi, $state, $stateParams) {
            var vm = this;
            vm.datasource = $stateParams.datasource || "DEMOdz";
            vm.tools = [{
                name: 'Markers + Patients',
                route: 'markers',
                img: 'markers.png',
                copy: 'Link copy number variation and mutation data to patients grouped by GBM classification: mesenchymal, classical, neural, proneural, and G-CIMP'
            }, {
                name: 'Timelines',
                route: 'timelines',
                img: 'timelines.png',
                copy: ''
            }, {
                name: 'Pathways',
                route: 'pathways',
                img: 'pathways.png',
                copy: 'Map patient specific expression levels on a hand curated network of genes associated with GBM. Click on edges to view the abstracts defining the relationship.'
            }, {
                name: 'Partial Least Squares Regression (PLSR)',
                route: 'plsr',
                img: 'plsr.png',
                copy: 'Use linear regression to correlate genes with clinical features using RNA expression.'
            }, {
                name: 'Principal Components Analysis (PCA)',
                route: 'pca',
                img: 'pca.png',
                copy: 'Two dimensional view of per sample expression data.'
            }, {
                name: 'Survival',
                route: 'survival',
                img: 'survival.png',
                copy: 'Compare survival rates of selected patients against the remaining population in a Kaplan Meier plot.'
            }, {
                name: 'Patient History',
                route: 'history',
                img: 'history.png',
                copy: ''
            }, {
                name: 'MetaData',
                route: 'metadata',
                img: 'metadata.png',
                copy: ''
            }];
            vm.explore = function(tool, datasource) {
                $state.go(tool, {
                    datasource: datasource
                });
            };
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osTimelines', timelines);

    /** @ngInject */
    function timelines() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/timelines/timelines.html',
            controller: TimelinesController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function TimelinesController(osApi, $state, $scope, $stateParams, $window, moment) {

            // View Model
            var vm = this;
            vm.datasource = $stateParams.datasource || "DEMOdz";
            vm.feature;
            vm.features;
            vm.eventTypes;
            vm.align;
            vm.sort;


            var color = function(d){
                        var status = d.name;
                        var rv = 
                            (status==="Birth") ?  "DARKBLUE" : 
                            (status==="Diagnosis") ? "DARKGREEN" :
                            (status==="Pathology") ? "DARKMAGENTA" :
                            (status==="Progression") ? "DARKORANGE" :
                            (status==="Absent") ? "DARKSLATEBLUE" :
                            (status==="Status") ? "DEEPPINK" :
                            (status==="Radiation") ? "LIMEGREEN" :
                            (status==="Procedure") ? "GOLD" :
                            (status==="Encounter") ? "MIDNIGHTBLUE" :
                            (status==="Drug") ? "MEDIUMSEAGREEN" :
                            "black";
                            return rv;
                    };


            
            var patients;
            vm.sortClick = function(v){
                vm.sort = v;
            };
            vm.eventTypeClick = function(eventType) {
                eventType.selected = !eventType.selected;
                chart.drawTimelines(patients, vm.eventTypes, vm.align);
            };
            vm.alignClick = function(eventType){
                vm.align = eventType;
                chart.drawTimelines(patients, vm.eventTypes, vm.align);
            };

            // VM Event Handlers
            vm.toggleFilter = function() {
                angular.element(".container-filters").toggleClass("container-filters-collapsed");
                angular.element(".container-filter-toggle").toggleClass("container-filter-toggle-collapsed");
            }
            var chart = (function() {

                var svgWidth = $("#timelines-chart").width()-100;
                var svgHeight = $("#timelines-chart").height();
                var rowHeight = 0;
                var svg = d3.select('#timelines-chart').append("svg")
                    .attr("width", "100%")
                    .attr("height", svgHeight);
                        
                var axisTimelineG;
                var axisFeatureG;

                var init = function(patients){
                    rowHeight = Math.floor(svgHeight / patients.length);
                    if (rowHeight<0) rowHeight = 2;
                    if (rowHeight>25) rowHeight = 25;
                
                    $(".timelines-lbl-events").css({'top' : ((patients.length * rowHeight) + 100) + 'px'});

                    svg.selectAll("rect.lines")
                        .data(patients)
                        .enter()
                        .append("rect")
                        .style({
                            "fill": "#e9e9e9"
                        })
                        .attr({
                            "transform": function(d, i) {return "translate(50," + ((i * rowHeight)+50) + ")";},
                            "class": "lines",
                            "x": 0,
                            "width": svgWidth,
                            "height": rowHeight - 1
                        })

                        var axisStyle = {
                            'shape-rendering': 'crispEdges',
                            'stroke': 'none'
                        };

                        var axisFeature = svg
                            .append("g")
                            .style(axisStyle)
                            .attr({
                                "class":"axisFeature axis",
                                "transform": "translate(50,49)",
                            }).call(
                                d3.svg.axis().scale(d3.scale.linear().domain([0, 1]).range([0, svgWidth])).orient('top')
                            );

                        var axisTimeline = svg.append("g")
                            .style(axisStyle)
                            .attr({
                                "class":"axisTimeline axis",
                                "transform": "translate(50,"+(49+(patients.length*rowHeight))+")"                                
                            }).call(
                                d3.svg.axis().scale(
                                    d3.scale.linear().domain([0, 1]).range([0, svgWidth])
                                ).orient('bottom')
                            );
                }
                var drawFeatures = function(patients, feature) {

                    // Define Feature Scale
                    var featureIndex = feature.index;
                    var featureScale = d3.scale.linear()
                        .domain([0, d3.max(patients.map(function(patient) {
                            return patient.calcEvents[featureIndex].value
                        }))])
                        .range([0, svgWidth]);

                    // Update Axis
                    var axisFeature = d3.svg.axis().scale(featureScale).orient('top');
                    var t = svg.transition().duration(750);
                    t.select('g.axisFeature').call(axisFeature);
                    
                    // Features
                    var svgFeatures = svg.selectAll("rect.feature")
                        .data(patients);

                    svgFeatures.exit().remove();

                    svgFeatures
                        .enter()
                        .append("rect")
                        .style({
                            "fill": "#b2e3fe"
                        })
                        .attr({
                            "transform": function(d, i) {return "translate(50," + ((i * rowHeight)+50) + ")";},
                            "class": "feature",
                            "x": 0,
                            "width": function(d) {
                                return featureScale(d.calcEvents[featureIndex].value);
                            },
                            "height": rowHeight - 1
                        })

                    svgFeatures
                        .transition()
                        .duration(300)
                         .attr({
                            "class": "feature",
                            "x": 0,
                            "width": function(d) { return featureScale(d.calcEvents[featureIndex].value); },
                            "height": rowHeight - 1
                        });
                }

                var drawTimelines = function(patientsOrig, eventTypes, align) {

                    // Filter Patients
                    // var patients = patientsOrig.filter(funciton(p){
                    //     console.log(p);
                    // })

                    // Function to Filter Date Events On Individual Patients
                    var filterEventTypes = eventTypes.filter( function(eventType) { return eventType.selected; });
                    var filterDateEvents = function(dateEvents){
                        return dateEvents.filter(function(dateEvent){
                            for (var i=0; i<filterEventTypes.length; i++){
                                if (filterEventTypes[i].name == dateEvent.name) return true;
                            }
                            return false;
                        });
                    };


                    // Loop Through Patients + Date Events To Adjust Start End

                    // patients.forEach(
                    //     function(patient){
                    //         var delta = patient["__"+align.name];
                    //         console.log(delta)
                    //     }
                    // );
   
                    // Define Timeline Scale
                    var timelineMin = patients
                        .reduce(function(p, c) {
                            return Math.min(p, filterDateEvents(c.dateEvents)
                                .reduce(function(p, c) {
                                        return Math.min(c.startValue, p)
                                    },
                                    Infinity))
                        }, Infinity);
                    var timelineMax = patients
                        .reduce(function(p, c) {
                            return Math.max(p, filterDateEvents(c.dateEvents)
                                .reduce(function(p, c) {
                                    var val = (c.endValue==null) ? c.startValue : c.endValue;
                                    return Math.max(val, p)
                                }, -Infinity))
                        }, -Infinity);
                    var timelineScale = d3.scale.linear()
                        .domain([timelineMin, timelineMax])
                        .range([0, svgWidth]);


                    // Update Axis
                    var axisTimeline = d3.svg.axis().scale(timelineScale).orient('bottom')
                        .tickFormat(function(d) { 
                            return moment.unix(d/1000).format("MM/DD/YYYY");
                        });
                    var t = svg.transition().duration(750);
                    t.select('g.axisTimeline').call(axisTimeline);


                var rows = svg.selectAll("g.timeline")
                .data( patients);

                var cells = rows.selectAll("rect.timeline")
                .data( function(d, i){ return filterDateEvents(d.dateEvents);  });

                // Cell Create
                 cells
                    .transition()
                    .duration(300)
                    .style({'opacity':1, 'fill':color})
                    .attr({
                        'class' : 'timeline',
                        "x": function(d) { return Math.round(timelineScale(d.startValue));},
                        'width':function(d){ return (d.endValue===null) ? 3 : (timelineScale(d.endValue) - timelineScale(d.startValue)); }
                    });

                // Cell Update
                cells
                    .enter()
                    .append("rect")
                    .style({'opacity': 0.0, 'fill':color})
                    .attr('class', 'enter timeline')
                    .attr({
                        'opacity': 0,
                        'height': (rowHeight - 1),
                        'width':function(d){ return (d.end===null) ? 3 : (timelineScale(d.endValue) - timelineScale(d.startValue)); }
                    })
                    .transition()
                    .duration(300)
                    .attr({
                        'opacity': 1.0,
                        'x': function(d) { return Math.round(timelineScale(d.startValue));},
                    });

                // Cell Delete
                cells
                    .exit()
                    .attr('class', 'timeline exit')
                    .transition()
                    
                    .duration(300)
                    .style('opacity', 0.0)
                    .attr({'x':0})
                    .remove();

                // Row
                var cells_in_new_rows = rows
                    .enter()
                    .append("g")
                    .attr('class', 'timeline enter')
                    .attr("transform", function(d, i) {return "translate(50," + ((i * rowHeight)+50) + ")";})
                    .selectAll('rect')
                    .data( function(d, i){ return filterDateEvents(d.dateEvents); } );

                // Row Create
                cells_in_new_rows.enter().append('rect')
                    .style('opacity', 0.0)
                    
                    .attr({
                        'opacity': 0,
                        'class': 'timeline enter',
                        'width': 100,
                        'height': (rowHeight - 1)
                    })
                    .style({'opacity': 0.0, 'fill':color})
                    .transition()
                    .duration(500)
                    .style('opacity', 1.0)
                    .attr({
                        'class': 'timeline enter',
                        'x': function(d) { return timelineScale(d.start) },
                        'width':function(d){ return (d.end===null) ? 3 : (timelineScale(d.endValue) - timelineScale(d.startValue)); }
                    });

                // Row Delete
                rows.exit()
                  .attr('class', 'timeline exit')
                  .transition()
                  .delay(200)
                  .duration(500)
                  .style('opacity', 0.0)
                  .remove();
                }

                return {
                    init: init,
                    drawFeatures: drawFeatures,
                    drawTimelines: drawTimelines
                }
            })();

     

            // Elements
            osApi.setBusy(true)("Loading Dataset");
            osApi.setDataset(vm.datasource).then(function() {
                osApi.getTimelines().then(function(response) {
                    var payload = response.payload;

                    // Load & Normalize Data (Should be done on server)
                    patients = payload.pts;
                    patients = Object.keys(patients).map(function(key) {
                        var val = patients[key];
                        val.id = key;
                        return val;
                    });
                    for (var i = 0; i < patients.length; i++) {

                        // Create References For Faster Subsequent Lookups
                        var p = patients[i];
                        var m = moment;
                        var mf = "YYYY-MM-DD"

                        // Map Start Dates
                        var dateSingle =
                            p.dateEvents
                            .filter(function(d) {
                                return d.eventOrder === 'single'
                            })
                            .map(function(d) {
                                return {
                                    "start": m(d.date, mf).unix() * 1000,
                                    "end": null,
                                    "name": d.name
                                };
                            });

                        // Map Start + End Dates
                        var endDates = p.dateEvents
                            .filter(function(d) { return d.eventOrder==='end' })
                            .map(function(d){ return {"end":m(d.date, mf).unix()*1000, "id":d.eventID}; });
                        var startDates = p.dateEvents
                            .filter(function(d) { return d.eventOrder==='start' })
                            .map(function(d){ return {"start":m(d.date, mf).unix()*1000,"name":d.name, "id":d.eventID}; });
                        startDates
                            .forEach(function(sd){ sd.end = endDates
                                .filter(function(ed) { return ed.id==sd.id} )[0].end });

                        // Join Single + Multiple Date Events
                        p.dateEvents = (startDates.length>0) ? startDates.concat(dateSingle) : dateSingle;
                        p.dateEvents.forEach(function(d){ p["__"+d.name] = d; });
                    }

                    // Set Collections
                    vm.features = patients[0].calcEvents.map(function(d, i) {
                        return {
                            "name": d.name,
                            "index": i
                        }
                    });
                    vm.eventTypes = Object.keys(response.payload.eventTypes).map(function(v) {
                        var rv =  {
                            "name": v,
                            "selected": true
                        };
                        rv.color = color(rv);
                        return rv;
                    });
                    chart.init(patients);

                    // Set Data
             
                    // Set State
                    vm.feature = vm.features[0];
                    vm.eventType = vm.eventTypes[0];
                    vm.sort = vm.features[0];
                    vm.align = vm.eventTypes[0];

                    // Watch Changes
                    $scope.$watch("vm.feature", function() {
                        chart.drawFeatures(patients, vm.feature)
                    });
               
                    $scope.$watch("vm.sort", function(){
                        var ceIndex = vm.sort.index;
                        patients = patients.sort(function(a, b){
                            var av = a.calcEvents[ceIndex].value;
                            var bv = b.calcEvents[ceIndex].value;
                            var rv = (av>bv) ? -1 : 1;
                            return rv;
                        });
                        chart.drawFeatures(patients, vm.feature);
                        chart.drawTimelines(patients, vm.eventTypes, vm.align);
                    })
                    osApi.setBusy(false);
                });
            });
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osSurvival', survival);

    /** @ngInject */
    function survival() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/survival/survival.html',
            controller: SurvivalController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function SurvivalController(osApi, osState, $state, $timeout, $scope, $stateParams) {

            var rawData;

            // View Model
            var vm = this;
            vm.datasource = $stateParams.datasource || "DEMOdz";
            if (osState.patientFilters.get()==null) osState.patientFilters.set(vm.datasource);
      

            // Load Datasets
            osApi.setBusy(true);
            osApi.setDataset(vm.datasource).then(function() {
                osApi.getPatientHistoryTable(vm.datasource).then(function(response) {
                    var ids = response.payload.tbl.map( function (d) { return d[0]; });
                    osApi.getCalculatedSurvivalCurves(ids, "").then(function(r){
                        document.getElementById("survival-img").src = r.payload;
                        osApi.setBusy(false);
                    });
                });
            });
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osPlsr', explore);

    /** @ngInject */
    function explore() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/plsr/plsr.html',
            controller: PlsrController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function PlsrController(osApi, $state, $stateParams, $timeout, $scope, d3, $sce) {

            // View Model
            var vm = this;
            vm.datasource = $stateParams.datasource || "DEMOdz";
            vm.deathMinFilter = vm.deathMinValue = 45;
            vm.deathMaxFilter = vm.deathMaxValue = 66;
            vm.survivalMinFilter = vm.survivalMinValue = 3;
            vm.survivalMaxFilter = vm.survivalMaxValue = 7;
            vm.geneSets = [];
            vm.geneSet = null;
            vm.frame;
            vm.tip = null;

            // VM Event Handlers
            vm.toggleFilter = function() {
                angular.element(".container-filters").toggleClass("container-filters-collapsed");
                angular.element(".container-filter-toggle").toggleClass("container-filter-toggle-collapsed");
            };
            vm.update = function() {
                update(true);
            };

            // Elements
            var elChart = angular.element("#plsr-chart");

            // Chart
            var chart = (function() {


                // Size
                var margin = {
                    top: 20,
                    right: 20,
                    bottom: 30,
                    left: 40
                };
                var width = elChart.width() - margin.left - margin.right;
                var height = elChart.height() - margin.top - margin.bottom;

                var svg = d3.select("#plsr-chart").append("svg")
                    .attr("id", "chart")
                    .attr("width", width)
                    .attr("height", height);

                var tooltip = svg.append("div")
                    .style("position", "absolute")
                    .style("z-index", "10")
                    .style("visibility", "hidden");

                var lines, circles, text;
                var xScale, yScale;

                function create(abs, vectors, genes) {
                    if (circles) circles.remove();
                    if (lines) lines.remove();
                    var nAbs = -1.0 * abs;

                    xScale = d3.scale.linear().domain([nAbs, abs]).range([0, width])
                    yScale = d3.scale.linear().domain([nAbs, abs]).range([height, 0])
                    text = svg.selectAll("text")
                        .data(vectors)
                        .enter().append("text")
                        .attr("class", "text")
                        .attr("x", function(v) {
                            return xScale(v[0]);
                        })
                        .attr("y", function(v) {
                            return yScale(v[1]);
                        })
                        .text(function(v) {
                            return v.name;
                        })
                        .attr("text-anchor",
                            function(v) {
                                var lr = (v[0] > 0) ? "start" : "end";
                                return lr;
                            })
                        .style("fill", "black");
                    lines = svg.selectAll("line")
                        .data(vectors)
                        .enter()
                        .append("line")
                        .attr("class", "line")
                        .style("stroke-width", 3)
                        .style("stroke", function(d) {
                            return (d.name.indexOf("Age")) ? "#1396de" : "#38347b"
                        })
                        .attr("x1", xScale(0))
                        .attr("y1", yScale(0))
                        .attr("x2", function(v) {
                            return xScale(v[0]);
                        })
                        .attr("y2", function(v) {
                            return yScale(v[1]);
                        });
                    circles = svg.selectAll("circle")
                        .data(genes)
                        .enter()
                        .append("circle")
                        .attr("cx", function(d) {
                            return xScale(d[0]);
                        })
                        .attr("cy", function(d) {
                            return yScale(d[1]);
                        })
                        .attr("r", function() {
                            return 3;
                        })
                        .style("fill", 'black')
                        .on("click", function(d) {
                            angular.element('#plsr-webpage').modal();
                            var url = "http://www.genecards.org/cgi-bin/carddisp.pl?gene=" + d.name;
                            $scope.$apply(function() {
                                vm.frame = $sce.trustAsResourceUrl(url);
                            });
                        })
                        .on("mouseover", function(d) {
                            var pt = d3.mouse(this);
                            tooltip
                                .text(d.name)
                                .style("top", (pt[1] + 160) + "px")
                                .style("left", (pt[0] + 10) + "px")
                                .style("visibility", "visible");
                        })
                        .on("mousemove", function() {
                            var pt = d3.mouse(this);
                            tooltip
                                .style("top", (pt[1] + 160) + "px")
                                .style("left", (pt[0] + 10) + "px");
                        })
                        .on("mouseout", function() {
                            return tooltip.style("visibility", "hidden");
                        });
                }

                function update(abs, vectors, genes) {

                    text
                        .data(vectors)
                        .transition()
                        .duration(900)
                        .attr("x", function(v) {
                            return xScale(v[0]);
                        })
                        .attr("y", function(v) {
                            return yScale(v[1]);
                        })


                    lines
                        .data(vectors)
                        .transition()
                        .duration(900)
                        .attr("x1", xScale(0))
                        .attr("y1", yScale(0))
                        .attr("x2", function(v) {
                            return xScale(v[0]);
                        })
                        .attr("y2", function(v) {
                            return yScale(v[1]);
                        });


                    circles
                        .data(genes) // Update with new data
                        .transition()
                        .duration(900)
                        .each("start", function() { // Start animation
                            d3.select(this) // 'this' means the current element
                                //.style("fill", "000")  // Change color
                                //.attr("r", 2);  // Change size
                        })
                        .delay(function(d, i) {
                            return i / genes.length * 500; // Dynamic delay (i.e. each item delays a little longer)
                        })
                        //.ease("linear")  // Transition easing - default 'variable' (i.e. has acceleration), also: 'circle', 'elastic', 'bounce', 'linear'
                        .attr("cx", function(d) {
                            return xScale(d[0]); // Circle's X
                        })
                        .attr("cy", function(d) {
                            return yScale(d[1]); // Circle's Y
                        })
                        .each("end", function() { // End animation
                            d3.select(this) // 'this' means the current element
                                .transition()
                                .duration(500)
                                .style("fill", "black") // Change color
                                .attr("r", 3); // Change radius
                        });
                }



                return {
                    create: create,
                    update: update

                }
            })("#plsr-chart");




            // Load Data
            osApi.setBusy(true)("Loading Dataset");
            osApi.setDataset(vm.datasource).then(function(response) {
                var mtx = response.payload.rownames.filter(function(v) {
                    return v.indexOf("mtx.mrna") >= 0
                });
                mtx = mtx[mtx.length - 1].replace(".RData", "");
                osApi.setBusyMessage("Creating PLSR Matrix");
                osApi.getPLSR(vm.datasource, mtx).then(function() {
                    osApi.setBusyMessage("Loading Gene Sets");
                    osApi.getGeneSetNames().then(function(response) {

                        // Load Gene Sets
                        vm.geneSets = response.payload;
                        vm.geneSet = vm.geneSets[0];
                        osApi.setBusyMessage("Loading Patients");
                        osApi.getSummarizedPLSRPatientAttributes().then(function(response) {

                            // Load Min Max Values
                            var payload = response.payload;
                            vm.deathMinValue = Math.floor(payload.AgeDx[0] / 365.24);
                            vm.deathMaxValue = Math.floor(payload.AgeDx[4] / 365.24);
                            vm.survivalMinValue = Math.floor(payload.Survival[0] / 365.24);
                            vm.survivalMaxValue = Math.floor(payload.Survival[4] / 365.24);
                            $scope.$watch('vm.geneSet', function() {
                                update(false);
                            });
                        });
                    });
                });
            });


            // API Call To Calculate PLSR
            var update = function(animate) {
                osApi.setBusyMessage("Calculating PLSR");
                var factors = [{
                    name: "Survival",
                    low: Number(vm.survivalMinFilter) * 365.24,
                    high: Number(vm.survivalMaxFilter) * 365.24
                }, {
                    name: "AgeDx",
                    low: Number(vm.deathMinFilter) * 365.24,
                    high: Number(vm.deathMaxFilter) * 365.24
                }];

                osApi.getCalculatedPLSR(vm.geneSet, factors).then(function(response) {
                    osApi.setBusyMessage("Rendering PLSR");

                    // Clean Up Data
                    var payload = response.payload;
                    var genes = payload.loadings.map(function(item, index) {
                        item.name = payload.loadingNames[index];
                        return item;
                    });
                    var vectors = payload.vectors.map(function(item, index) {
                        item.name = payload.vectorNames[index];
                        return item;
                    });
                    var abs = payload.maxValue * 1.2;


                    if (animate) chart.update(abs, vectors, genes);
                    else chart.create(abs, vectors, genes);
                    osApi.setBusy(false);
                });
            };

        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osPca', explore);

    /** @ngInject */
    function explore() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/pca/pca.html',
            controller: PcaController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function PcaController(osApi, $state, $stateParams, $timeout, $scope, d3) {

            // State
            var vm = this;
            vm.datasource = $stateParams.datasource || "DEMOdz";
            vm.geneSets = [];
            vm.geneSet = null;
            vm.toggleFilter = function() {
                angular.element(".container-filters").toggleClass("container-filters-collapsed");
                angular.element(".container-filter-toggle").toggleClass("container-filter-toggle-collapsed");
            }

            // Elements
            var elChart = angular.element("#pca-chart");
            var d3Chart = d3.select("#pca-chart")
                .append("svg")
                .attr("id", "chart");
            var d3xAxis = d3Chart.append("g");
            var d3yAxis = d3Chart.append("g");

            // Initalizae
            osApi.setBusy(true)("Loading Dataset");
            osApi.setDataset(vm.datasource).then(function(response) {

                var mtx = response.payload.rownames.filter(function(v) {
                    return v.indexOf("mtx.mrna") >= 0
                });

                mtx = mtx[mtx.length - 1].replace(".RData", "");
                osApi.setBusyMessage("Creating PCA Matrix");
                osApi.getPCA(vm.datasource, mtx).then(function() {
                    osApi.setBusyMessage("Loading Gene Sets");
                    osApi.getGeneSetNames().then(function(response) {

                        // Load Gene Sets
                        vm.geneSets = response.payload;
                        vm.geneSet = vm.geneSets[0];
                        $scope.$watch('vm.geneSet', function() {
                            update();
                        });
                    });
                });
            });

            // API Call To Calculate PCA
            var update = function() {
                osApi.setBusyMessage("Calculating PCA");
                osApi.getCalculatedPCA(vm.geneSet).then(function(response) {
                    osApi.setBusyMessage("Rendering PCA");
                    var payload = response.payload;
                    vm.pc1 = response.payload["importance.PC1"];
                    vm.pc2 = response.payload["importance.PC2"];
                    draw(payload.scores)
                    osApi.setBusy(false);
                });
            };

            // Render
            var draw = function(dataset) {
                var width = elChart.width();
                var height = elChart.height();

                var max, min;
                max = d3.max(dataset, function(d) {
                    return +d[0];
                });
                min = d3.min(dataset, function(d) {
                    return +d[0];
                });
                var xMax = ((Math.abs(max) > Math.abs(min)) ? max : min) * 1.2;
                max = d3.max(dataset, function(d) {
                    return +d[1];
                });
                min = d3.min(dataset, function(d) {
                    return +d[1];
                });
                var yMax = ((Math.abs(max) > Math.abs(min)) ? max : min) * 1.2;

                var xScale = d3.scale.linear()
                    .domain([-xMax, xMax])
                    .range([0, width]);

                var yScale = d3.scale.linear()
                    .domain([-yMax, yMax])
                    .range([height, 0]);

                var xAxis = d3.svg.axis()
                    .scale(xScale)
                    .orient("top")
                    .ticks(5);

                var yAxis = d3.svg.axis()
                    .scale(yScale)
                    .orient("left")
                    .ticks(5);

                d3Chart
                    .attr("width", width)
                    .attr("height", height);


                var circles = d3Chart.selectAll("circle").data(dataset, function(d) {
                    return d;
                })
                circles.enter()
                    .append("circle")
                    .attr("cx", width * .5)
                    .attr("cy", height * .5)
                    .attr("r", function() {
                        return 3;
                    })
                    .style("fill-opacity", "0")
                    .transition()
                    .duration(750)
                    .delay(function(d, i) {
                        return i / 300 * 500;
                    })
                    .attr("cx", function(d) {
                        return xScale(d[0]);
                    })
                    .attr("cy", function(d) {
                        return yScale(d[1]);
                    })
                    .style("fill-opacity", 1)
                circles.exit()
                    .transition()
                    .duration(600)
                    .delay(function(d, i) {
                        return i / 300 * 500;
                    })
                    .attr("cx", width * .5)
                    .attr("cy", height * .5)
                    .style("fill-opacity", "0")
                    .remove();


                d3xAxis
                    .attr("class", "axis")
                    .attr("transform", "translate(0, " + yScale(0) + ")")
                    .call(xAxis)
                    .append("text")
                    .text("PC1");

                d3yAxis
                    .attr("class", "axis")
                    .attr("transform", "translate(" + xScale(0) + ", 0)")
                    .call(yAxis)
                    .append("text")
                    .attr("y", 10)
                    .attr("dy", ".71em")
                    .text("PC2");


            };
        }
    }
})();

