(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osDatasource', datasource);

    /** @ngInject */
    function datasource() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/datasource/datasource.html',
            controller: DatasourceController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function DatasourceController(osApi, $state, osState) {
            var vm = this;
            vm.datasets = [];
            vm.explore = function(tool, datasource) {
                osState.setDatasource(datasource);
                $state.go(tool, {
                    datasource: datasource
                });
            };

            // Load Datasets
            osApi.setBusy(true);
            osApi.getDataSetNames().then(function(response) {
                vm.datasets = response.payload.datasets;
                osApi.setBusy(false);
            });
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .run(runBlock);

    /** @ngInject */
    function runBlock($log, $rootScope) {

        // Actions To Take On State Change
        $rootScope.$on('$stateChangeStart', function() {

            // Reset DataTable Custom Filters
            angular.element.fn.DataTable.ext.search = [];

        });

        $log.debug('runBlock end');
    }

})();

